<template>
  <v-container>
    <v-row class="top-bar"><v-btn @click="back()" text>&lt; Back To {{from || 'DEALS'}}</v-btn></v-row>
    <coin-summary :coin="coin" currency="BTC" />
    <product :item="getContractById(id, coin)">
      <template v-slot:s1="{ item, currency }">
        {{currency === 'USD' ? item.priceUsd : item.priceBtc | unitPrice(coin, currency, currency === 'USD' ? 4 : 8)}}
        <span class="subtitle-1">
          {{'_' | unit(item.coin)}}
        </span>
      </template>
      <template v-slot:s2="{ item }">
        <div class="d-flex discount flex-column align-self-center">
          <span class="dealMeta">Discount</span>
          <colored class="body-1 font-weight-bold" :value="item.discount" format="percent" />
        </div>
      </template>
    </product>

    <product-chart :item="getContractById(id, coin)" />

    <product-detail :item="getContractById(id, coin)">
      <template v-slot:income="{ item }">
        <tr><td>Mining Earnings</td><td><colored :value="item.payoff" :coin="coin" :digits="4" /></td></tr>
      </template>
      <template v-slot:cost="{ item }">
        <tr><td>Daily Fee</td><td><colored :value="-item.electricityFee || -item.priceUsd" :coin="coin" :digits="4"/></td></tr>
      </template>
      <template v-slot:profit="{ item }">
        <tr><td>Net Profit</td><td><colored :value="item.payoff - item.priceUsd" :coin="coin" :digits="4" /></td></tr>
        <tr><td>Premium / Discount</td><td><colored :value="item.discount" format="percent" /></td></tr>
      </template>
    </product-detail>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";

import { price, unit, unitPrice } from "../filters";
import CoinSummary from "../components/CoinSummary";
import Colored from "../components/Colored";
import ProductChart from "../components/ProductChart";
import Product from "../components/product/Product";
import ProductDetail from "../components/product/ProductDetail";

export default {
  name: "ProductRealtimeView",
  filters: { price, unit, unitPrice },
  components: { CoinSummary, Colored, ProductChart, Product, ProductDetail },
  methods: {
    back() { history.back(); }
  },
  computed: {
    ...mapGetters("contracts", ["getContractById"])
  },
  data() {
    const { from, id, coin } = this.$route.query;
    return {
      from,
      id,
      coin
    };
  }
}
</script>

<style scoped>
  .container {
    padding-top: 0!important;
  }

  .top-bar {
    border-bottom: 1px solid lightgrey;
    margin-bottom: 10px;
  }
</style>