<template>
  <div ref="chart" style="height: 400px;">loading...</div>
</template>

<script>
import _ from "lodash";
import axios from "axios";
import Highcharts from "highcharts";

import { mapState } from "vuex";

const initExporter = _.once(() => {
  const ex = require('highcharts/modules/exporting');
  const ed =  require('highcharts/modules/export-data');

  ex(Highcharts);
  ed(Highcharts);
});

export default {
  name: "ProductChart",
  props: ["item"],
  computed: {
    ...mapState("product", ["currency"])
  },
  async mounted() {
    this.updateChart();
    const { exporting } = this.$route.query;
    if (exporting) {
      initExporter();
    }
  },
  watch: {
    currency() {
      this.updateChart();
    },
    item() {
      this.updateChart();
    }
  },
  methods: {
    async updateChart() {
      if (!this.item) return ;
      if (!this.chartData) {
        const { data } = await axios.get(`https://8xiscecoe2.execute-api.ap-northeast-1.amazonaws.com/production/hashpower/nicehash/history?id=${this.item.id}`);
        this.chartData = data;
      }

      const { chartData } = this;
      const algo = _.last(this.item.id.split("-"));
      const priceFactor = algo === 'SHA256' ? 10 ** 12 : 10 ** 9;
      const hashUnit = algo === 'SHA256' ? 'TH' : 'GH';

      Highcharts.chart(this.$refs.chart, {
        title: {
          text: 'Historical Unit Price & Hashrate',
          style: {
            color: 'white'
          }
        },

        credits: {
          enabled: false
        },

        chart: {
          backgroundColor: '#000',
          style: {
            fontFamily: '"Roboto", Helvetica, Arial, sans-serif'
          }
        },

        colors: ['#2b908f', '#cece4b'],

        yAxis: [
          {
            title: {
              text: `Hashrate(${hashUnit}/s)`,
              style: { color: 'white' }
            },
            labels: {
              style: { color: '#ccc' }
            },
            min: 0,
          }, {
            title: {
              text: `Price(${this.currency}/${hashUnit})`,
              style: { color: 'white' }
            },
            labels: {
              style: { color: '#ccc' }
            },
            min: 0,
            opposite: true
          }
        ],

        tooltip: {
          shared: true
        },

        xAxis: {
          type: 'datetime',
          labels: {
            style: { color: '#ccc' }
          },
          dateTimeLabelFormats: {
            hour: '%b %d',
            day: '%b %d'
          },
          endOnTick: false
        },

        legend: {
          enabled: false
        },

        plotOptions: {
          series: {
            label: {
              connectorAllowed: false
            }
          }
        },

        series: [{
          name: '',
          type: 'area',
          yAxis: 0,
          tooltip: {
            valueDecimals: 2,
            valueSuffix: `${hashUnit}`
          },
          data: chartData.map(v => [v.hour * 1000, v.volume / priceFactor])
        }, {
          name: '',
          yAxis: 1,
          tooltip: {
            valueDecimals: 8,
            valueSuffix: `${this.currency}/${hashUnit}/day`
          },
          data: chartData.map(v => [v.hour * 1000, (this.currency === 'USD' ? v.priceUsd : v.priceBtc) * priceFactor])
        }],
      });
    }
  }
}
</script>

